<template>
  <ul class="boy">
    <li v-for="m in mes">
      <img :src="m.imgs" alt="">
      <div class="name">{{m.name}}</div>
      <div class="price"><span class="new">{{m.new}}</span><span class="old">{{m.old}}</span></div>
      <a></a>
    </li>
  </ul>
</template>

<script>
    export default {
      name: "First",
      data(){
        return{
          mes:[
            {
              "imgs":"./static/images/boy1.jpg",
              "name":" | 2018",
              "new":"59",
              "old":"260"
            },
            {
              "imgs":"./static/images/boy2.jpg",
              "name":" | ",
              "new":"59",
              "old":"79"
            },
            {
              "imgs":"./static/images/boy3.jpg",
              "name":" | ",
              "new":"69",
              "old":"119"
            },
            {
              "imgs":"./static/images/boy4.jpg",
              "name":" | T",
              "new":"35",
              "old":"259"
            },
            {
              "imgs":"./static/images/boy5.jpg",
              "name":" | IP2018",
              "new":"139",
              "old":"400"
            },
            {
              "imgs":"./static/images/boy6.jpg",
              "name":" | -",
              "new":"53",
              "old":"140"
            }
          ]
        }
      }
    }
</script>

<style lang="scss" scoped>
.boy{
  width: 100%;
  padding-left: .08rem;
  box-sizing: border-box;
  margin-bottom: .19rem;
  overflow: hidden;
  display: flex;
  overflow-x: scroll;
  &::-webkit-scrollbar{
    height: 0;
  }
  li{
    width: 40%;
    border: 1px solid;
    padding: .05rem;
    box-sizing: border-box;
    margin-right: .1rem;
    flex-shrink: 0;
    img{
      width: 100%;
    }
    .name{
      font-size: .13rem;
      overflow : hidden;
      display: -webkit-box;
      text-overflow: ellipsis;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      margin-top: .06rem;
      color: #585c64;
    }
    .price{
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      margin: .02rem 0;
      .new{
        font-size: .14rem;
      }
      .old{
        font-size: .12rem;
        color: rgb(152, 152, 159);
        text-decoration: line-through;
      }
    }
    a{
      display: inline-block;
      width: 100%;
      height: .26rem;
      text-align: center;
      line-height: .26rem;
      background-color:  #dd3d97;
      border-radius: .26rem;
      color: #fff;
    }
  }
}
</style>



