<template>
  <div id="Vbee-login">
    <div id="user-header">
      <!---->
      <a href="#">
        <router-link to="/my">
         <i class="iconfont icon-cuohao"></i>
        </router-link>
      </a>
      <!--logo-->
      <img src="../../../static/images/loginLogo.png" alt="">
    </div>
    <!---->
    <div id="user-form">
      <div class="target-title">
        <p id="targetCon"></p>
      </div>
      <!---->
      <div class="user-name">
        <input type="text" id="username" placeholder=" /  / ">
      </div>
      <!---->
      <div class="user-pass">
        <!---->
        <img id="demo_img" src="../../../static/images/eyeclose.png" @click="hideShowPsw()">
        <input type="password" id="demo_input" placeholder=""/>
      </div>
      <!---->
      <div class="forget-pass">
        <a href="#"></a>
      </div>
      <!---->
      <div class="user-btn">
        <input type="submit" value="  " @click="hasinfo">
      </div>
      <!---->
      <div class="user-phone-register">
        <a href="#"></a>
        <a href="#"></a>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "VbeeLogin",

    methods:{
      hideShowPsw:function () {
        // jsjquery
        var demoImg = document.getElementById("demo_img")
        var demoInput = document.getElementById("demo_input")

        //text blockpassword block
        if (demoInput.type == "password") {
          demoInput.type = "text";
          demoImg.src = "./static/images/eyeopen.png";
        }else {
          demoInput.type = "password";
          demoImg.src = "./static/images/eyeclose.png";
        }
      },

      // input
      hasinfo:function(){
        var demoName = document.getElementById("username")
        var demoInput = document.getElementById("demo_input")
        var demoText = document.getElementById("targetCon")
        console.log(demoName.value)
        console.log(demoInput.value)
        if(demoName.value =="" || demoInput.value == ""){
          demoText.style.display = "block"
          demoText.innerHTML ="";
        }else if(demoName.onblur || demoInput.onblur){
          demoText.style.display = "none"
          demoText.innerHTML = ""
        }else{
          demoText.style.display = "none"
          demoText.innerHTML = ""
        }

      },
      //input 

    }
  }
</script>

<style scoped>

  #user-header a .iconfont{
    color:#0e1010;
    font-size: .25rem;
    display: block;
    margin:.16rem .14rem;
  }
  #user-header img{
    width: 1.25rem;
    height: .5rem;
    display: block;
    margin:0 auto;
  }
  #user-form{
    padding:.42rem .18rem 0;
    display: flex;
    flex-direction: column;
  }
  /**/
  #user-form .target-title p{
    font-size: .1rem;
    color:#e32b1f;
    display: none;
  }
  /**/
  #user-form .user-name{
    border-bottom: 2px solid #d3d3d8;
  }
  /**/
  #user-form .user-pass{
    border-bottom: 2px solid #d3d3d8;
    position: relative;
  }
  /**/
  #demo_img{
    width: .15rem;
    height: .11rem;
    display: block;
    position: absolute;
    right: .14rem;
    top:.2rem;
  }
 /*forminput*/
  #user-form input{
    width: 100%;
    height: .52rem;
    font-size: .13rem;
    color:#c3c2c3;
    border:none;
    outline: none;
    line-height: .52rem;
    display: flex;
    text-indent: .05rem;
  }
  /**/
  .forget-pass{
    display: flex;
    justify-content:flex-end;
    height: .32rem;
    align-items: center;
    padding-bottom: .09rem;
  }
  .forget-pass a{
    font-size: .1rem;
    color:#87888d;
  }
  /*.*/
  .user-btn input{
    height: .4rem;
    background: #dd3d95;
    color:#f4add7;
    font-size: .12rem;
    display: flex;
    justify-content: center;
  }
  /**/
  .user-phone-register{

    display: flex;
    justify-content: space-between;
    margin-top:.2rem;
  }
  .user-phone-register a{
    font-size: .11rem;
    color:#3883dc;
  }
</style>



